import * as yup from 'yup';

export default yup.object().shape({
  oldPassword: yup
    .string()
    .min(6, '     6')
    .max(50, '      50 ')
    .required('  '),
  newPassword: yup
    .string()
    .min(6, '     6')
    .max(50, '      50 ')
    .required('  '),
  confirmedNewPassword: yup
    .string()
    .min(6, '     6')
    .max(50, '      50 ')
    .required('    '),
});
