import React, { useContext, useEffect } from 'react';
import { Switch, Route } from 'react-router-dom';
import jwt_decode from 'jwt-decode';
import { observer } from 'mobx-react-lite';

import CssBaseline from '@material-ui/core/CssBaseline';
import {
  MuiThemeProvider,
  createMuiTheme,
  makeStyles,
  Theme,
} from '@material-ui/core/styles';
import { lightTheme, darkTheme } from '../config/theme';
import { RootStoreContext } from '../stores/rootStore';

import { ToastContainer } from 'react-toastify';
import PrivateRoute from './PrivateRoute';
import Sidebar from './Sidebar';
import Topbar from './Topbar';
import Overlay from './Overlay';
import Spinner from './Spinner';
import NotFound from './NotFound';
import TaskList from '../../features/tasks/list/TaskList';
import Profile from '../../features/user/Profile';
import TaskDetails from '../../features/tasks/details/TaskDetails';
import { Theme as MyTheme } from '../models/common';

const useStyles = makeStyles((theme: Theme) => ({
  app: {
    position: 'relative',
    padding: `${theme.spacing(2)}px`,
  },
  '@global': {
    '*::-webkit-scrollbar': {
      width: '0.4em',
      height: '0.4em',
    },
    '*::-webkit-scrollbar-track': {
      '-webkit-box-shadow': 'inset 0 0 6px rgba(0,0,0,0.00)',
    },
    '*::-webkit-scrollbar-thumb': {
      backgroundColor: 'rgba(0,0,0,.1)',
      outline: '1px solid #ccc',
    },
  },
}));

const App = () => {
  const rootStore = useContext(RootStoreContext);
  const { theme, setAppLoaded, access_token, refresh_token, appLoaded } =
    rootStore.commonStore;
  const { refreshToken, fillDisplayNameFromToken } = rootStore.userStore;
  const classes = useStyles();

  useEffect(() => {
    if (access_token && refresh_token) {
      const decoded: any = jwt_decode(access_token);
      const currentTime = Date.now() / 1000;
      if (decoded.exp < currentTime) refreshToken();
      else {
        fillDisplayNameFromToken(access_token, refresh_token);
      }
    }
    setAppLoaded();
  }, [
    access_token,
    refresh_token,
    refreshToken,
    setAppLoaded,
    fillDisplayNameFromToken,
  ]);

  if (!appLoaded) return <Spinner />;

  return (
    <MuiThemeProvider
      theme={createMuiTheme(theme === MyTheme.dark ? darkTheme : lightTheme)}
    >
      <CssBaseline />
      <ToastContainer position="bottom-right" />
      <Topbar />
      <PrivateRoute path="/" component={Sidebar} />
      <div className={classes.app}>
        <Switch>
          <Route exact path="/not-found" component={NotFound} />
          <PrivateRoute exact path="/profile" component={Profile} />
          <PrivateRoute exact path="/tasklist" component={TaskList} />
          <PrivateRoute
            exact
            path="/tasklist/:loanApplicationTaskId"
            component={TaskDetails}
          />
        </Switch>
      </div>
      <Overlay />
    </MuiThemeProvider>
  );
};

export default observer(App);
