import * as yup from 'yup';

export default yup.object().shape({
  firstname: yup
    .string()
    .max(50, '     50 ')
    .required(" ''   "),
  lastname: yup
    .string()
    .max(50, '     50 ')
    .required(" ''   "),
  middlename: yup
    .string()
    .max(50, '     50 ')
    .required(" ''   "),
  birthDate: yup
    .date()
    .nullable()
    .transform((curr, orig) => orig === '' ? null : curr)
    .min(new Date(1900, 0, 1), '  01.01.1900')
    .max(new Date(), '    ')
    .required(" ' '   "),
  email: yup
    .string()
    .nullable()
    .email('    ')
    .max(50, '     50 '),
  phone: yup
    .string()
    .nullable()
    .max(20, '     20 '),
});
