import { action, observable, runInAction } from 'mobx';
import { RootStore } from './rootStore';
import {
  IChangePasswordFormValues,
  IProfile,
  IProfileFormValues,
} from '../models/profile';
import agent from '../api/agent';
import { toast } from 'react-toastify';

export default class ProfileStore {
  rootStore: RootStore;

  constructor(rootStore: RootStore) {
    this.rootStore = rootStore;
  }

  @observable loading = false;
  @observable profile: IProfile | null = null;

  @action loadProfile = async () => {
    try {
      this.loading = true;

      var result = await agent.Profile.get();
      runInAction(() => {
        this.profile = result.data;
        this.loading = false;
      });
    } catch (ex) {
      runInAction('load profile', () => {
        this.profile = null;
        this.loading = false;
        if (ex.data) {
          toast.error(ex.data.message);
        }
      });
    }
  };

  @action updateProfile = async (data: IProfileFormValues) => {
    try {
      this.loading = true;

      var formData = new FormData();
      formData.append('firstname', data.firstname);
      formData.append('lastname', data.lastname);
      formData.append('middlename', data.middlename);
      formData.append(
        'birthDate',
        `${data.birthDate.getDate()}-${
          data.birthDate.getMonth() + 1
        }-${data.birthDate.getFullYear()}`,
      );
      formData.append('email', data.email);
      formData.append('phone', data.phone);
      formData.append('image', data.image);

      var result = await agent.Profile.update(formData);
      runInAction(() => {
        this.profile = result.data;
        this.loading = false;
        toast.success(result.message);
      });
    } catch (ex) {
      runInAction('update profile', () => {
        this.profile = null;
        this.loading = false;
        throw ex.data;
      });
    }
  };

  @action changePassword = async (data: IChangePasswordFormValues) => {
    try {
      this.loading = true;

      var result = await agent.Profile.changePassword(data);
      runInAction(() => {
        this.loading = false;
        toast.success(result.message);
      });
    } catch (ex) {
      runInAction('update profile', () => {
        this.loading = false;
        throw ex.data;
      });
    }
  };
}
