import React, { useContext } from 'react';
import { useTranslation } from "react-i18next";
import { observer } from 'mobx-react-lite';
import { RootStoreContext } from '../../../app/stores/rootStore';

// Material UI
import TablePagination from '@material-ui/core/TablePagination';

const TaskListPagination = () => {
  const { t } = useTranslation();
  const rootStore = useContext(RootStoreContext);
  const { 
    page, 
    pageLimit, 
    count,
    handlePageChange, 
    handlePageLimitChange
  } = rootStore.loanApplicationListStore;

  return (
    <TablePagination
      rowsPerPageOptions={[5, 10, 25]}
      component="div"
      count={count}
      page={page}
      rowsPerPage={pageLimit}
      onChangePage={handlePageChange}
      onChangeRowsPerPage={handlePageLimitChange}
      labelRowsPerPage={t('COMPONENTS.TABLE_PAGINATION.ROWS_PER_PAGE')}
      nextIconButtonText={t('COMPONENTS.TABLE_PAGINATION.NEXT')}
      backIconButtonText={t('COMPONENTS.TABLE_PAGINATION.BACK')}
      labelDisplayedRows={({ from, to, count }) => t('COMPONENTS.TABLE_PAGINATION.DISPLAYED_ROWS')
        .replace('{from}', from.toString())
        .replace('{to}', to.toString())
        .replace('{count}', count.toString())}
    />
  )
}

export default observer(TaskListPagination);
