import React, { useContext, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { useForm, Controller } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import { RootStoreContext } from '../../app/stores/rootStore';
import { IChangePasswordFormValues } from '../../app/models/profile';
import passwordValidationScheme from '../../app/validators/passwordValidationScheme';

import { makeStyles, Theme } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import Alert from '@material-ui/lab/Alert';

const useStyles = makeStyles((theme: Theme) => ({
  root: {
    padding: theme.spacing(1),
    display: 'flex',
    flexFlow: 'column wrap',
    justifyContent: 'center',
  },
  input: {
    minWidth: 300,
    marginBottom: theme.spacing(1),
  },
  helperText: {
    color: 'red',
  },
  submitButton: {
    marginBottom: theme.spacing(2),
  },
  alert: {
    width: '100%',
    margin: `-${theme.spacing(2)}px 0 ${theme.spacing(2)}px`,
  },
}));

interface IProps {
  callback: () => void;
}

const ChangePassword: React.FC<IProps> = ({ callback }) => {
  const rootStore = useContext(RootStoreContext);
  const { changePassword, loading } = rootStore.profileStore;

  const classes = useStyles();
  const { t } = useTranslation();

  const [error, setError] = useState<string | null>(null);

  const { handleSubmit, control, errors } = useForm<IChangePasswordFormValues>({
    resolver: yupResolver(passwordValidationScheme),
    reValidateMode: 'onBlur',
    mode: 'onBlur',
  });

  const onSubmit = handleSubmit(async (data) => {
    try {
      await changePassword(data);
      callback();
    } catch (ex) {
      if (ex && 'Message' in ex) setError(ex.Message);
    }
  });

  return (
    <form className={classes.root} onSubmit={onSubmit}>
      <Controller
        name="oldPassword"
        control={control}
        defaultValue=""
        render={({ value, onChange, onBlur }) => (
          <TextField
            value={value}
            type="password"
            onChange={onChange}
            onBlur={onBlur}
            label={t('COMPONENTS.CHANGE_PASSWORD.OLD_PASSWORD')}
            helperText={errors?.oldPassword?.message ?? ' '}
            className={classes.input}
            FormHelperTextProps={{
              className: classes.helperText,
            }}
            variant="outlined"
            fullWidth
          />
        )}
      />
      <Controller
        name="newPassword"
        control={control}
        defaultValue=""
        render={({ value, onChange, onBlur }) => (
          <TextField
            value={value}
            type="password"
            onChange={onChange}
            onBlur={onBlur}
            label={t('COMPONENTS.CHANGE_PASSWORD.NEW_PASSWORD')}
            helperText={errors?.newPassword?.message ?? ' '}
            className={classes.input}
            FormHelperTextProps={{
              className: classes.helperText,
            }}
            variant="outlined"
            fullWidth
          />
        )}
      />
      <Controller
        name="confirmedNewPassword"
        control={control}
        defaultValue=""
        render={({ value, onChange, onBlur }) => (
          <TextField
            value={value}
            type="password"
            onChange={onChange}
            onBlur={onBlur}
            label={t('COMPONENTS.CHANGE_PASSWORD.CONFIRMED_NEW_PASSWORD')}
            helperText={errors?.confirmedNewPassword?.message ?? ' '}
            className={classes.input}
            FormHelperTextProps={{
              className: classes.helperText,
            }}
            variant="outlined"
            fullWidth
          />
        )}
      />
      {error && (
        <Alert className={classes.alert} severity="error">
          {error}
        </Alert>
      )}
      <Button
        color="primary"
        variant="outlined"
        type="submit"
        size="large"
        className={classes.submitButton}
        disabled={Object.keys(errors).length > 0 || loading}
      >
        {t('COMMON.SAVE')}
      </Button>
    </form>
  );
};

export default ChangePassword;
