import React from 'react';
import { useTranslation } from 'react-i18next';
import {
  ICreditHistory,
  ILoanApplicationDetails,
  IOrganization,
  IPerson,
} from '../../../../app/models/loanApplication';
import UniversalTable from '../../../../app/common/tables/UniversalTable';

// Material UI
import { makeStyles, Theme } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import { isEmpty } from 'lodash';
import { format } from 'date-fns/esm';
import { ITableData, ITableHeader } from '../../../../app/models/common';

const useStyles = makeStyles((theme: Theme) => ({
  root: {},
  title: {
    fontWeight: 'bold',
    margin: theme.spacing(2, 0, 1),
    [theme.breakpoints.up('md')]: {
      fontSize: '1.8rem',
    },
  },
  fieldTitle: {
    fontWeight: 'bold',
    margin: theme.spacing(1),
  },
  listItem: {
    margin: theme.spacing(1),
  },
  table: {
    margin: theme.spacing(3, 0),
  },
  pkb: {
    margin: theme.spacing(2, 0),
  },
}));

interface IProps {
  data: ILoanApplicationDetails;
}

const ClientDetails: React.FC<IProps> = ({
  data: { clientDetails, application },
}) => {
  const classes = useStyles();
  const { t } = useTranslation();

  const renderTitle = (title: string) => (
    <Typography variant="h6" className={classes.title}>
      {title}
    </Typography>
  );

  const renderLine = (title: string, text?: string) => {
    if (isEmpty(text)) return null;

    return (
      <Typography>
        <span className={classes.fieldTitle}>{title}:</span>
        <span>{text}</span>
      </Typography>
    );
  };

  const renderPerson = (title: string, person?: IPerson) => {
    if (isEmpty(person) || person === undefined) return null;

    return (
      <div>
        {renderTitle(title)}
        {renderLine(t('COMPONENTS.CLIENT_DETAILS.FULLNAME'), person.fullName)}
        {renderLine(t('COMPONENTS.CLIENT_DETAILS.CITIZENSHIP'), person.country)}
        {renderLine(t('COMPONENTS.CLIENT_DETAILS.IIN'), person.identifier)}
        {person.identificationDocument &&
          renderLine(
            t('COMPONENTS.CLIENT_DETAILS.PASSPORT'),
            `${person.identificationDocument.number}, ${
              person.identificationDocument.issuer
            }, ${format(
              new Date(person.identificationDocument.dateIssue),
              'dd.MM.yyyy',
            )}`,
          )}
        {renderLine(
          t('COMPONENTS.CLIENT_DETAILS.ADDRESS'),
          person.address?.register,
        )}
        {renderLine(
          t('COMPONENTS.CLIENT_DETAILS.WORK_PHONE_NUMBER'),
          person.phone.work,
        )}
        {renderLine(
          t('COMPONENTS.CLIENT_DETAILS.MOBILE_PHONE_NUMBER'),
          person.phone.mobile,
        )}
        {renderLine(
          t('COMPONENTS.CLIENT_DETAILS.HOME_PHONE_NUMBER'),
          person.phone.home,
        )}
        {renderLine(
          t('COMPONENTS.CLIENT_DETAILS.BIRTH_DATE'),
          format(new Date(person.birthDate), 'dd.MM.yyyy'),
        )}
        {renderLine(
          t('COMPONENTS.CLIENT_DETAILS.BIRTH_PLACE'),
          person.birthPlace,
        )}
        {renderLine(t('COMPONENTS.CLIENT_DETAILS.EDUCATION'), person.education)}
        {renderLine(
          t('COMPONENTS.CLIENT_DETAILS.WORK_EXPERIENCE_TOTAL'),
          person.workExperience?.total,
        )}
        {renderLine(
          t('COMPONENTS.CLIENT_DETAILS.WORK_EXPERIENCE_AGRICULTURE'),
          person.workExperience?.agriculture,
        )}
        {renderLine(
          t('COMPONENTS.CLIENT_DETAILS.MARRIAGE_STATUS'),
          person.marriageStatus,
        )}
        {renderLine(t('COMPONENTS.CLIENT_DETAILS.SPOUSE'), person.spouse)}
      </div>
    );
  };

  const generateCreditHistoryTableData = (
    creditHistory: ICreditHistory[],
  ): ITableData => {
    const header: ITableHeader[] = [
      {
        code: 'fullName',
        name: t('COMPONENTS.CLIENT_DETAILS.CREDIT_ORGANIZATION_FULLNAME'),
        isOrderBy: true,
        orderByDirection: 'asc',
      },
      {
        code: 'sum',
        name: t('COMPONENTS.CLIENT_DETAILS.CREDIT_SUM'),
        isOrderBy: false,
        orderByDirection: 'asc',
      },
      {
        code: 'dateIssue',
        name: t('COMPONENTS.CLIENT_DETAILS.CREDIT_ISSUE_DATE'),
        isOrderBy: false,
        orderByDirection: 'asc',
      },
      {
        code: 'period',
        name: t('COMPONENTS.CLIENT_DETAILS.CREDIT_PERIOD'),
        isOrderBy: false,
        orderByDirection: 'asc',
      },
      {
        code: 'balance',
        name: t('COMPONENTS.CLIENT_DETAILS.CREDIT_BALANCE'),
        isOrderBy: false,
        orderByDirection: 'asc',
      },
    ];

    return {
      header,
      body: creditHistory,
    };
  };

  const generateAffiliatedCompaniesTableData = (
    organizations: IOrganization[],
  ): ITableData => {
    const header: ITableHeader[] = [
      {
        code: 'fullName',
        name: t('COMPONENTS.CLIENT_DETAILS.AFFILIATED_ORGANIZATION_FULLNAME'),
        isOrderBy: true,
        orderByDirection: 'asc',
      },
      {
        code: 'bank_accounts',
        name: t(
          'COMPONENTS.CLIENT_DETAILS.AFFILIATED_ORGANIZATION_BANK_ACCOUNTS',
        ),
        isOrderBy: false,
        orderByDirection: 'asc',
      },
      {
        code: 'share_in_company',
        name: t(
          'COMPONENTS.CLIENT_DETAILS.AFFILIATED_ORGANIZATION_SHARE_IN_COMPANY',
        ),
        isOrderBy: false,
        orderByDirection: 'asc',
      },
      {
        code: 'debts',
        name: t('COMPONENTS.CLIENT_DETAILS.AFFILIATED_ORGANIZATION_DEBT_SUM'),
        isOrderBy: false,
        orderByDirection: 'asc',
      },
    ];

    return {
      header,
      body: organizations.map((org) => ({
        fullName: `${org.fullName}, ${org.address?.register}`,
        bank_accounts: org.bankAccounts
          .map((ba) => `: ${ba.bic}, : ${ba.number}`)
          .join('; '),
        share_in_company: org.shareInCapital,
        debts: org.debts
          .map((d) => `: ${d.bic}, c: ${d.debt}`)
          .join('; '),
      })),
    };
  };

  // TODO: Split into seperate components
  return (
    <Grid container className={classes.root}>
      <Grid item xs={12} sm={6}>
        <div>
          {renderTitle(t('COMPONENTS.CLIENT_DETAILS.ORGANIZATION'))}
          {renderLine(
            t('COMPONENTS.CLIENT_DETAILS.ORGANIZATION_FULLNAME'),
            clientDetails.organization.fullName,
          )}
          {renderLine(
            t('COMPONENTS.CLIENT_DETAILS.REGISTER_ADDRESS'),
            clientDetails.organization.address?.register,
          )}
          {renderLine(
            t('COMPONENTS.CLIENT_DETAILS.FACT_ADDRESS'),
            clientDetails.organization.address?.fact,
          )}
          {renderLine(
            t('COMPONENTS.CLIENT_DETAILS.WORK_PHONE_NUMBER'),
            clientDetails.organization.phone?.work,
          )}
          {renderLine(
            t('COMPONENTS.CLIENT_DETAILS.MOBILE_PHONE_NUMBER'),
            clientDetails.organization.phone?.mobile,
          )}
          {renderLine(
            t('COMPONENTS.CLIENT_DETAILS.HOME_PHONE_NUMBER'),
            clientDetails.organization.phone?.home,
          )}
          {renderLine(
            t('COMPONENTS.CLIENT_DETAILS.FAX'),
            clientDetails.organization.fax,
          )}
          {renderLine(
            t('COMPONENTS.CLIENT_DETAILS.EMAIL'),
            clientDetails.organization.email,
          )}
          {renderLine(
            t('COMPONENTS.CLIENT_DETAILS.IIN_BIN'),
            clientDetails.organization.identifier,
          )}
          {renderLine(
            t('COMPONENTS.CLIENT_DETAILS.OKED'),
            clientDetails.organization.oked.join(', '),
          )}
          {renderLine(
            t('COMPONENTS.CLIENT_DETAILS.ORGANIZATION_TYPE'),
            clientDetails.organization.organizationType,
          )}
          {renderLine(
            t('COMPONENTS.CLIENT_DETAILS.OWNERSHIP_FORM'),
            clientDetails.organization.ownershipForm,
          )}
          {renderLine(
            t('COMPONENTS.CLIENT_DETAILS.PARENT_COMPANY'),
            clientDetails.organization.parent,
          )}
          {clientDetails.organization.identificationDocument &&
            renderLine(
              t('COMPONENTS.CLIENT_DETAILS.IDENTIFICATION_DOCUMENT'),
              `${clientDetails.organization.identificationDocument.number}, ${clientDetails.organization.identificationDocument.issuer}, ${clientDetails.organization.identificationDocument.dateIssue}`,
            )}
          {clientDetails.organization.registrationDocument &&
            renderLine(
              t('COMPONENTS.CLIENT_DETAILS.REGISTRATION_DOCUMENT'),
              `${clientDetails.organization.registrationDocument.number}, ${clientDetails.organization.registrationDocument.issuer}, ${clientDetails.organization.registrationDocument.dateIssue}`,
            )}
        </div>

        {renderPerson(
          t('COMPONENTS.CLIENT_DETAILS.BENEFICIARY'),
          clientDetails.beneficiary,
        )}
        {renderPerson(
          t('COMPONENTS.CLIENT_DETAILS.REPRESENTATIVE'),
          clientDetails.representative,
        )}

        {clientDetails.contacts.length > 0 && (
          <div>
            {renderTitle(t('COMPONENTS.CLIENT_DETAILS.CONTACTS'))}
            {clientDetails.contacts.map((contact, i) => {
              return (
                <div key={i} className={classes.listItem}>
                  {renderLine(
                    t('COMPONENTS.CLIENT_DETAILS.FULLNAME'),
                    contact.fullName,
                  )}
                  {renderLine(
                    t('COMPONENTS.CLIENT_DETAILS.REGISTER_ADDRESS'),
                    contact.address?.register,
                  )}
                  {renderLine(
                    t('COMPONENTS.CLIENT_DETAILS.PHONE'),
                    contact.phone?.mobile,
                  )}
                </div>
              );
            })}
          </div>
        )}
        {clientDetails.organization.bankAccounts.length > 0 && (
          <div>
            {renderTitle(t('COMPONENTS.CLIENT_DETAILS.BANK_ACCOUNTS'))}
            {clientDetails.organization.bankAccounts.map((bankAccount, i) => {
              return (
                <div key={i} className={classes.listItem}>
                  {renderLine(
                    t('COMPONENTS.CLIENT_DETAILS.BANK_ACCOUNT_BIC'),
                    bankAccount.bic,
                  )}
                  {renderLine(
                    t('COMPONENTS.CLIENT_DETAILS.BANK_ACCOUNT_NUMBER'),
                    bankAccount.number,
                  )}
                </div>
              );
            })}
          </div>
        )}
      </Grid>
      <Grid item xs={12} sm={6}>
        {renderPerson(t('COMPONENTS.CLIENT_DETAILS.HEAD'), clientDetails.head)}

        {renderPerson(
          t('COMPONENTS.CLIENT_DETAILS.BOOKER'),
          clientDetails.booker,
        )}
      </Grid>

      <Grid item xs={12}>
        <div className={classes.table}>
          <UniversalTable
            title={t('COMPONENTS.CLIENT_DETAILS.CREDIT_HISTORY')}
            data={generateCreditHistoryTableData(
              clientDetails.organization.creditHistory,
            )}
          />
        </div>
      </Grid>
      <Grid item xs={12}>
        <div className={classes.table}>
          <UniversalTable
            title={t('COMPONENTS.CLIENT_DETAILS.AFFILIATED_ORGANIZATIONS')}
            data={generateAffiliatedCompaniesTableData(
              clientDetails.organization.affiliatedOrganizations,
            )}
          />
        </div>
      </Grid>

      {application.pkbFile && (
        <Grid item xs={12} className={classes.pkb}>
          <Typography variant="h6" className={classes.title}>
              :
          </Typography>
          <Link
            href={`/${application.pkbFile.url}`}
            download={application.pkbFile.filename}
          >
            {application.pkbFile.filename}
          </Link>
        </Grid>
      )}
    </Grid>
  );
};

export default ClientDetails;
