import React from 'react';
import { useTranslation } from 'react-i18next';
import { IAssetsTable } from '../../../../app/models/loanApplication';
import UniversalTable from '../../../../app/common/tables/UniversalTable';

// Material UI
import { makeStyles, Theme } from '@material-ui/core/styles';

const useStyles = makeStyles((theme: Theme) => ({
  root: {
  },
}))

interface IProps {
  data: IAssetsTable
}

const Assets: React.FC<IProps> = ({ data }) => {
  const classes = useStyles();
  const { t } = useTranslation();

  return (
    <div className={classes.root}>
      <UniversalTable title={t('COMPONENTS.ASSETS.LAND')} data={data.land} handleOrderingChange={(code, direction) => console.log(code, direction)} />
      <UniversalTable title={t('COMPONENTS.ASSETS.BIO')} data={data.bio} handleOrderingChange={(code, direction) => console.log(code, direction)} />
      <UniversalTable title={t('COMPONENTS.ASSETS.FLORA')} data={data.flora} handleOrderingChange={(code, direction) => console.log(code, direction)} />
      <UniversalTable title={t('COMPONENTS.ASSETS.TECH')} data={data.tech} handleOrderingChange={(code, direction) => console.log(code, direction)} />
    </div>
  )
}

export default Assets
