import { observable, computed, action, runInAction } from 'mobx';
import jwt_decode from 'jwt-decode';
import agent from '../api/agent';
import { RootStore } from './rootStore';
import { history } from '../..';
import { IUser, IUserFormValues } from '../models/user';

export default class UserStore {
  rootStore: RootStore;

  constructor(rootStore: RootStore) {
    this.rootStore = rootStore;
  }

  @observable user: IUser | null = null;
  @observable loginModalOpen = false;
  @observable pending = false;

  @computed get isLoggedIn() {
    return !!this.user;
  }

  @action login = async (values: IUserFormValues) => {
    try {
      this.pending = true;

      const result = await agent.User.login(values);
      runInAction('user login', () => {
        this.user = result.data;
        this.loginModalOpen = false;
        this.pending = false;

        this.rootStore.commonStore.setTokens(
          result.data.accessToken,
          result.data.refreshToken,
        );
        history.push('/tasklist');
      });
    } catch (error) {
      runInAction('user login', () => {
        this.pending = false;
        throw error.data;
      });
    }
  };

  @action register = async (values: IUserFormValues) => {
    try {
      const result = await agent.User.register(values);
      runInAction(() => {
        this.user = result.data;
        this.rootStore.commonStore.setTokens(
          result.data.accessToken,
          result.data.refreshToken,
        );
        history.push('/tasklist');
      });
    } catch (error) {
      throw error;
    }
  };

  @action refreshToken = async () => {
    try {
      const result = await agent.User.refresh();

      runInAction(() => {
        this.user = result.data;
        this.rootStore.commonStore.setTokens(
          result.data.accessToken,
          result.data.refreshToken,
        );
        history.push('/tasklist');
      });
    } catch (error) {
      this.logout();
    }
  };

  @action logout = () => {
    this.rootStore.commonStore.setTokens(null, null);
    this.rootStore.commonStore.setSidebarOpenState(false);
    this.user = null;
    history.push('/');
  };

  @action setLoginModalOpenState = (state: boolean) => {
    this.loginModalOpen = state;
  };

  @action fillDisplayNameFromToken = (
    access_token: string,
    refresh_token: string,
  ) => {
    const decoded: any = jwt_decode(access_token);

    this.user = {
      displayName:
        `${decoded.lastName} ${decoded.firstName} ${decoded.middleName}`.trim(),
      accessToken: access_token,
      refreshToken: refresh_token,
    };
  };
}
