import 'date-fns';
import React, { useContext, useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { observer } from 'mobx-react-lite';
import { RootStoreContext } from '../../app/stores/rootStore';
import { useForm, Controller } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import { IProfileFormValues } from '../../app/models/profile';
import profileValidationScheme from '../../app/validators/profileValidationScheme';
import DateFnsUtils from '@date-io/date-fns';
import {
  MuiPickersUtilsProvider,
  KeyboardDatePicker,
} from '@material-ui/pickers';
import InputMask from 'react-input-mask';
import UniversalDialog from '../../app/common/dialogs/UniversalDialog';
import ChangePassword from './ChangePassword';

import { makeStyles, Theme } from '@material-ui/core/styles';
import Tooltip from '@material-ui/core/Tooltip';
import Zoom from '@material-ui/core/Zoom';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import AddIcon from '@material-ui/icons/Add';
import VpnKeyIcon from '@material-ui/icons/VpnKey';

const useStyles = makeStyles((theme: Theme) => ({
  root: {
    padding: theme.spacing(4),
  },
  title: {
    marginBottom: theme.spacing(6),
    display: 'flex',
    flexFlow: 'row nowrap',
    justifyContent: 'space-between',
    alignItems: 'center',
  },
  form: {
    display: 'flex',
    flexFlow: 'row wrap',
    justifyContent: 'center',
  },
  leftSide: {
    flex: 1,
    maxWidth: 400,
  },
  rightSide: {
    flex: 2,
    maxWidth: 1000,
  },
  formGroup: {
    marginTop: 40,
    display: 'flex',
    flexFlow: 'row wrap',
    justifyContent: 'space-evenly',
    alignItems: 'center',
  },
  input: {
    width: 300,
  },
  helperText: {
    color: 'red',
    height: 50,
  },
  image: {
    width: 280,
    height: 280,
    position: 'relative',
    '& > img': {
      width: '100%',
      height: '100%',
      borderRadius: '50%',
      border: '30px solid #fefefe',
    },
    [theme.breakpoints.down('sm')]: {
      margin: '0 auto',
    },
  },
  addButtonImage: {
    position: 'absolute',
    left: '50%',
    bottom: 0,
    backgroundColor: theme.palette.primary.main,
    border: `3px solid ${theme.palette.common.white}`,
    transform: 'translate(-50%, 25%)',
    '&:hover': {
      backgroundColor: theme.palette.primary.dark,
    },
  },
  addButtonIcon: {
    color: theme.palette.common.white,
  },
  changePasswordButton: {
    marginLeft: 'auto',
  },
  submitButton: {
    margin: `${theme.spacing(2)}px auto`,
    display: 'block',
  },
}));

const Profile = () => {
  const rootStore = useContext(RootStoreContext);
  const { loadProfile, profile, loading, updateProfile } =
    rootStore.profileStore;

  const [modalOpen, setModalOpen] = useState(false);
  const [imageSrc, setImageSrc] = useState('/assets/profile-default.png');

  const classes = useStyles();
  const { t } = useTranslation();

  const { handleSubmit, control, errors, setValue, watch } =
    useForm<IProfileFormValues>({
      defaultValues: {
        firstname: profile?.firstname,
        lastname: profile?.lastname,
        middlename: profile?.middlename,
        birthDate: profile !== null ? profile.birthDate : new Date(),
        email: profile?.email,
        phone: profile?.phone,
        image: undefined,
      },
      resolver: yupResolver(profileValidationScheme),
      reValidateMode: 'onBlur',
      mode: 'onBlur',
    });

  useEffect(() => {
    if (profile === null) {
      loadProfile();
    } else {
      setValue('firstname', profile.firstname);
      setValue('lastname', profile.lastname);
      setValue('middlename', profile.middlename);
      setValue('birthDate', new Date(profile.birthDate));
      setValue('email', profile.email);
      setValue('phone', profile.phone);
      if (profile.image !== null)
        setImageSrc(
          process.env.REACT_APP_BPM_API_URL_ROOT + profile.image?.url,
        );
    }
  }, [loadProfile, profile, setValue]);

  const onSubmit = handleSubmit(async (data) => {
    try {
      updateProfile(data);
    } catch (ex) {
      // TODO: set form errors
      console.log(ex);
    }
  });

  const imageValue = watch('image');

  return (
    <div className={classes.root}>
      <div className={classes.title}>
        <Typography variant="h6">{t('COMPONENTS.PROFILE.TITLE')}</Typography>
        <Tooltip
          title={t('COMPONENTS.PROFILE.CHANGE_PASSWORD').toString()}
          TransitionComponent={Zoom}
        >
          <Button
            variant="outlined"
            color="primary"
            className={classes.changePasswordButton}
            onClick={() => setModalOpen(true)}
          >
            <VpnKeyIcon color="primary"></VpnKeyIcon>
          </Button>
        </Tooltip>
      </div>
      <form className={classes.form} onSubmit={onSubmit}>
        <MuiPickersUtilsProvider utils={DateFnsUtils}>
          <div className={classes.leftSide}>
            <div className={classes.image}>
              <img
                src={imageValue ? URL.createObjectURL(imageValue) : imageSrc}
                alt="profile"
              />
              <label htmlFor="image">
                <Tooltip
                  title={t('COMPONENTS.PROFILE.CHANGE_IMAGE').toString()}
                  TransitionComponent={Zoom}
                >
                  <IconButton className={classes.addButtonImage} component="span">
                    <AddIcon className={classes.addButtonIcon} />
                  </IconButton>
                </Tooltip>
              </label>
              <Controller
                name="image"
                control={control}
                defaultValue={null}
                render={() => (
                  <input
                    id="image"
                    accept="image/*"
                    type="file"
                    onChange={(e) => setValue('image', e!.target!.files![0])}
                    hidden
                  />
                )}
              />
            </div>
          </div>
          <div className={classes.rightSide}>
            <div className={classes.formGroup}>
              <Controller
                name="firstname"
                control={control}
                defaultValue=""
                render={({ value, onChange, onBlur }) => (
                  <TextField
                    value={value}
                    onChange={onChange}
                    onBlur={onBlur}
                    label={t('COMMON.FIRST_NAME')}
                    helperText={errors?.firstname?.message ?? ' '}
                    className={classes.input}
                    FormHelperTextProps={{
                      className: classes.helperText,
                    }}
                    variant="outlined"
                  />
                )}
              />
              <Controller
                name="lastname"
                control={control}
                defaultValue=""
                render={({ value, onChange, onBlur }) => (
                  <TextField
                    value={value}
                    onChange={onChange}
                    onBlur={onBlur}
                    label={t('COMMON.LAST_NAME')}
                    helperText={errors?.lastname?.message ?? ' '}
                    className={classes.input}
                    FormHelperTextProps={{
                      className: classes.helperText,
                    }}
                    variant="outlined"
                  />
                )}
              />
              <Controller
                name="middlename"
                control={control}
                defaultValue=""
                render={({ value, onChange, onBlur }) => (
                  <TextField
                    value={value}
                    onChange={onChange}
                    onBlur={onBlur}
                    label={t('COMMON.MIDDLE_NAME')}
                    helperText={errors?.middlename?.message ?? ' '}
                    className={classes.input}
                    FormHelperTextProps={{
                      className: classes.helperText,
                    }}
                    variant="outlined"
                  />
                )}
              />
              <Controller
                name="birthDate"
                control={control}
                defaultValue=""
                render={({ value, onChange, onBlur }) => (
                  <KeyboardDatePicker
                    disableToolbar
                    variant="inline"
                    inputVariant="outlined"
                    format="dd/MM/yyyy"
                    className={classes.input}
                    id="birth-date-picker"
                    label={t('COMMON.BIRTH_DATE')}
                    value={value}
                    onChange={onChange}
                    onBlur={onBlur}
                    KeyboardButtonProps={{
                      'aria-label': 'change date',
                    }}
                    helperText={errors?.birthDate?.message ?? ' '}
                    FormHelperTextProps={{
                      className: classes.helperText,
                    }}
                  />
                )}
              />
              <Controller
                name="email"
                control={control}
                defaultValue=""
                render={({ value, onChange, onBlur }) => (
                  <TextField
                    value={value}
                    type="email"
                    onChange={onChange}
                    onBlur={onBlur}
                    label={t('COMMON.EMAIL')}
                    helperText={errors?.email?.message ?? ' '}
                    className={classes.input}
                    FormHelperTextProps={{
                      className: classes.helperText,
                    }}
                    variant="outlined"
                  />
                )}
              />
              <Controller
                name="phone"
                control={control}
                defaultValue=""
                render={({ value, onChange, onBlur }) => (
                  <InputMask
                    mask="8 (999) 999 99 99"
                    value={value}
                    onChange={onChange}
                    onBlur={onBlur}
                  >
                    {(inputProps: any) => (
                      <TextField
                        {...inputProps}
                        value={value}
                        onChange={onChange}
                        onBlur={onBlur}
                        label={t('COMMON.PHONE')}
                        helperText={errors?.phone?.message ?? ' '}
                        className={classes.input}
                        FormHelperTextProps={{
                          className: classes.helperText,
                        }}
                        variant="outlined"
                      />
                    )}
                  </InputMask>
                )}
              />
            </div>
            <div>
              <Button
                color="primary"
                variant="outlined"
                type="submit"
                size="large"
                className={classes.submitButton}
                disabled={Object.keys(errors).length > 0 || loading}
              >
                {t('COMMON.SAVE')}
              </Button>
            </div>
          </div>
        </MuiPickersUtilsProvider>
      </form>
      <UniversalDialog
        open={modalOpen}
        onClose={() => setModalOpen(false)}
        title={t('COMPONENTS.PROFILE.CHANGE_PASSWORD')}
        content={<ChangePassword callback={() => setModalOpen(false)} />}
      />
    </div>
  );
};

export default observer(Profile);
