import { RootStore } from './rootStore';
import { reaction, observable, action } from 'mobx';
import { ru, enUS, kk } from 'date-fns/locale';
import i18n from '../config/i18n';
import { Lang, Theme } from '../models/common';

export default class CommonStore {
  rootStore: RootStore;

  constructor(rootStore: RootStore) {
    this.rootStore = rootStore;

    reaction(
      () => this.access_token,
      (token) => {
        if (token == null) window.localStorage.removeItem('jwt_access');
        else window.localStorage.setItem('jwt_access', token);
      }
    );

    reaction(
      () => this.refresh_token,
      (token) => {
        if (token == null) window.localStorage.removeItem('jwt_refresh');
        else window.localStorage.setItem('jwt_refresh', token);
      }
    );
  }

  @observable access_token: string | null = window.localStorage.getItem('jwt_access');
  @observable refresh_token: string | null = window.localStorage.getItem('jwt_refresh');
  @observable appLoaded = false;
  @observable lang: Lang = Lang.ru; // i18n.language
  @observable locale: Locale = ru;
  @observable theme: Theme = Theme.light;
  @observable overlayOpen = false;
  @observable sideBarOpen = false;
  @observable isLoggedIn = false;

  @action setTokens = (access_token: string | null, refresh_token: string | null) => {
    this.access_token = access_token;
    this.refresh_token = refresh_token;
  };

  @action setAppLoaded = () => {
    this.appLoaded = true;
  };

  @action setLanguage = (lang: Lang) => {
    i18n.changeLanguage(lang);
    this.lang = lang;
    switch (lang) {
      case 'kk':
        this.locale = kk;
        break;
      case 'en':
        this.locale = enUS;
        break;
      case 'ru':
      default:
        this.locale = ru;
    }
  };

  @action setTheme = (theme: Theme) => {
    this.theme = theme;
  };

  @action setOverlayOpenState = (state: boolean) => {
    this.overlayOpen = state;
  }

  @action setSidebarOpenState = (state: boolean) => {
    this.sideBarOpen = state;
  }
}
