import { action, computed, observable, reaction, runInAction } from 'mobx';
import { toast } from 'react-toastify';
import agent from '../api/agent';
import { OrderDirection } from '../models/common';
import {
  ILoanApplicationListItem,
  ILoanApplicationNavigation,
} from '../models/loanApplication';
import { RootStore } from './rootStore';

export default class LoanApplicationListStore {
  rootStore: RootStore;

  constructor(rootStore: RootStore) {
    this.rootStore = rootStore;

    reaction(
      () => this.page,
      async () => await this.getLoanApplications(),
    );

    reaction(
      () => this.pageLimit,
      async () => await this.getLoanApplications(),
    );

    reaction(
      () => this.order,
      async () => await this.getLoanApplications(),
    );

    reaction(
      () => this.navigationParams,
      async () => await this.getLoanApplications(),
    );
  }

  @observable pending = false;
  @observable page = 0;
  @observable pageLimit = 10;
  @observable order: OrderDirection = 'desc';
  @observable orderBy: keyof ILoanApplicationListItem = 'appointmentDate';
  @observable search: string = '';
  @observable count = 0;
  @observable loanApplications: Partial<ILoanApplicationListItem>[] = [];
  @observable loanStatus: string | null = null;
  @observable userRole: string | null = null;
  @observable navigationPending = false;
  @observable navigationItems: ILoanApplicationNavigation[] = [];

  @computed get navigationParams() {
    return {
      loanStatus: this.loanStatus,
      userRole: this.userRole,
    };
  }

  @action handlePageChange = (_: unknown, newPage: number) => {
    this.page = newPage;
  };

  @action handlePageLimitChange = (
    event: React.ChangeEvent<HTMLInputElement>,
  ) => {
    this.pageLimit = parseInt(event.target.value);
  };

  @action handleOrderingChange = (property: keyof ILoanApplicationListItem) => {
    const isAsc = this.orderBy === property && this.order === 'asc';
    this.order = isAsc ? 'desc' : 'asc';
    this.orderBy = property;
  };

  @action handleSearchChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    this.search = event.target.value;
  };

  @action getLoanApplications = async () => {
    try {
      this.pending = true;
      const result = await agent.LoanApplication.getList({
        page: this.page + 1,
        pageLimit: this.pageLimit,
        orderBy: this.orderBy,
        order: this.order,
        search: this.search,
        statusId: this.loanStatus,
        roleId: this.userRole,
      });

      runInAction(() => {
        this.pending = false;
        this.count = result.data.count;
        this.loanApplications = result.data.list;
      });
    } catch (ex) {
      runInAction(() => {
        this.pending = false;
        if (ex.data) {
          toast.error(ex.data.message);
        }
      });
    }
  };

  @action getNavigationItems = async () => {
    try {
      this.navigationPending = true;
      const result = await agent.LoanApplication.getNavigationItems();

      runInAction(() => {
        this.navigationPending = false;
        this.navigationItems = result.data;
      });
    } catch (ex) {
      runInAction(() => {
        this.navigationPending = false;
        if (ex.data) {
          toast.error(ex.data.message);
        }
      });
    }
  };

  @action selectNavigationItem = (
    userRole: string | null,
    loanStatus: string | null,
  ) => {
    this.userRole = userRole;
    this.loanStatus = loanStatus;
  };
}
